.game-container {
  width: 100%;
  height: 100vh;
  display: -ms-grid;
  display: grid;
  -ms-grid-rows: 20vh 5fr 1fr;
      grid-template-rows: 20vh 5fr 1fr;
  position: relative;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: #1c1c1c;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../../assets/woodTexture.jpg");
  background-size: cover;
  background-position: center;
  overflow: hidden;
}

.game-field {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #d37035;
  -webkit-animation: rattle 0.2s;
          animation: rattle 0.2s;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

.lower-game-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100%;
  width: 100%;
  bottom: 0.5vh;
}

.lower-game-container .self-avatar {
  width: 30vh;
  height: 30vh;
  border-radius: 100rem;
  overflow: hidden;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  display: block;
  left: 0;
  bottom: 0;
  z-index: 1;
  background-color: #1e1a1a;
}

.lower-game-container .self-avatar img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.slam {
  -webkit-animation: slam 1s;
          animation: slam 1s;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
  color: #1f0e0e;
  display: block;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 13em;
  font-weight: 900;
  position: relative;
  text-align: center;
  text-transform: uppercase;
}

.slam {
