@-webkit-keyframes slam {
  0% {
    -webkit-transform: scale(10, 10);
            transform: scale(10, 10);
    opacity: 0;
  }
  40% {
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
    opacity: 1;
  }
}

@keyframes slam {
  0% {
    -webkit-transform: scale(10, 10);
            transform: scale(10, 10);
    opacity: 0;
  }
  40% {
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
    opacity: 1;
  }
}

